.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}



body {
  background-color: lightgray;  
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.App-header {
  background-color: #222;
  padding: 2rem 0 2rem 0;
  color: white;
  width: 100%;
}

#ul-list {
  padding-left: 0;
}

.list-item {
  background-color: lightskyblue;
  margin-top: 1rem;
  display: grid;
  grid-template-columns: 7% 75% 8% 10%;
  border-radius: .5rem;
  -webkit-box-shadow: 2px 5px 5px gray;
          box-shadow: 2px 5px 5px gray;
  list-style-type: none;
  margin-left: auto;
  margin-right: auto;
}



.item1 {
  grid-column: 1;
  
}

.item2 {
  grid-column: 2;
  padding-left: 3rem;
  text-align: left;
}

.item3 {
  grid-column: 3;
  text-align: right;
  padding-right: 1rem;
}

.item4 {
  grid-column: 4;
  text-align: left;
}

@media (min-width: 356px)
{
#add-button {
  margin-left: 1rem;
  padding: .5rem;
  
}
}

@media (max-width: 355px) {
  #add-button {
    margin: 1rem;
    padding: 1rem;
  }
}




#todo-label {
  margin-right: .5rem;
}



@media (min-width: 601px) {
  .list-item {
    width: 50%;
    padding: 2rem;
  }
}



@media (max-width: 600px)
{
  .list-item {
    width: 90%;
    padding: 1rem;
  }
}






